@import "./nullstyle";
@import "./variable";
@import "./fonts";
@import "./mixins";

* {
  font-family: $font-family;
  line-height: 120%;
}

html {
  scroll-behavior: smooth;
}

.container {
  max-width: 1300px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
}

.button {
  background: transparent;
  padding: 14px 18px;
  border: 2px solid rgba(91, 88, 85, 0.3);
  border-radius: 4px;
  color: $primary-color-dark;
  @include transition;
  &:hover {
    background: $primary-color-dark;
    color: $contrast-color;
    border-color: $primary-color-dark;
  }
}

.header {
  position: relative;
  background: url(../img/header__bg.png) no-repeat center center;
  background-size: cover;
  padding: 20px 0 40px 0;
  overflow: hidden;
  &__top {
    @include flex(space-between);
    margin-bottom: 150px;
  }
  &__menu {
    @include flex(space-between);
    max-width: 670px;
    width: 100%;
  }
  &__link {
    display: inline-block;
    font-size: 15px;
    color: $contrast-color;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 2px;
      background: $contrast-color;
      transform: scaleX(0);
      transform-origin: right;
      transition: transform 0.5s;
    }
    &:hover {
      &::after {
        transform: scaleX(1);
        transform-origin: left;
      }
    }
  }
  &__button {
    max-width: 230px;
    width: 100%;
    @include flex(space-between);
    button {
      padding: 8px 16px;
      background: $transparent;
      color: $contrast-color;
      font-size: 15px;
      border-radius: 4px;
      @include transition;
      &:hover {
        background: $primary-color-dark;
      }
    }
  }
  .burger-btn {
    display: none;
    z-index: 10;
    position: relative;
    width: 30px;
    height: 20px;
    cursor: pointer;
    > span {
      position: absolute;
      background-color: $primary-color;
      width: 100%;
      height: 3px;
      border-radius: 18px;
      left: 0;
      top: 9px;
      @include transition;
    }
    &:after,
    &:before {
      content: "";
      position: absolute;
      background-color: $primary-color;
      width: 100%;
      height: 3px;
      border-radius: 18px;
      left: 0;
      @include transition;
    }
    &:before {
      top: 20px;
    }
    &:after {
      bottom: 20px;
    }
    &_active {
      span {
        opacity: 0;
      }
      &:before {
        transform: rotate(45deg);
        top: 9px;
      }
      &:after {
        transform: rotate(-45deg);
        bottom: 9px;
      }
    }
  }
  &__bottom {
    @include flex($flex-direction: column);
    text-align: center;
  }
  &__title {
    font-size: 54px;
    color: $contrast-color;
    margin-bottom: 50px;
  }
  &__browse {
    padding: 11px 18px;
    background: $primary-color-dark;
    color: $contrast-color;
    font-size: 16px;
    border-radius: 4px;
    margin-bottom: 115px;
    @include transition;
  }
  &__faq {
    max-width: 370px;
    width: 100%;
    @include flex(space-between);
    a {
      @include flex(space-between);
      svg {
        margin-left: 20px;
      }
    }
  }
  &__pop-up {
    position: absolute;
    right: 25px;
    bottom: 40px;
    max-width: 310px;
    width: 100%;
    padding: 30px 25px;
    text-align: left;
    background: $primary-color;
    box-shadow: 0px 2px 12px rgba(23, 35, 33, 0.1);
    border-radius: 2px;
    @include transition;
    h3 {
      font-size: 16px;
      color: $contrast-color;
      margin-bottom: 8px;
    }
    p {
      font-size: 14px;
      color: $contrast-color;
      margin-bottom: 15px;
    }
    button {
      padding: 13px 18px;
      background: $transparent;
      color: $contrast-color;
      font-size: 14px;
      border-radius: 4px;
    }
  }
  @media screen and (max-width: 768px) {
    &__menu {
      position: absolute;
      max-width: none;
      text-align: center;
      z-index: 2;
      // top: -225%;
      transform: scaleY(0);
      transform-origin: top;
      left: 0;
      top: -30%;
      width: 100%;
      padding: 70px 20px 20px 20px;
      background: $primary-color-dark;
      @include flex($flex-direction: column);
      @include transition;
      &_active {
        top: 0;
        transform: scaleY(1);
        box-shadow: 0 0 25px 40px rgb(0 0 0 / 25%);
      }
    }
    &__link {
      margin-bottom: 10px;
    }
    &__button {
      flex-direction: column;
      button {
        &:first-child {
          margin-bottom: 10px;
        }
      }
    }
    .burger-btn {
      display: block;
    }
    &__title {
      font-size: 36px;
    }
  }
  @media screen and (max-width: 500px) {
    &__top {
      margin-bottom: 50px;
    }
    &__title {
      font-size: 21px;
      margin-bottom: 25px;
    }
    &__browse {
      margin-bottom: 40px;
    }
  }
  @media screen and (max-width: 350px) {
    padding-bottom: 20px;
    &__title {
      font-size: 18px;
    }
    &__faq {
      flex-direction: column;
    }
  }
}

//                                    WHY-ALMA

.why-alma {
  padding: 65px 0 35px 0;
  @include flex(space-between, $align-items: unset);
  &__left {
    max-width: 415px;
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  &__title {
    color: $primary-color;
    font-size: 16px;
    text-transform: uppercase;
    margin-bottom: 15px;
  }
  &__sub-title {
    font-size: 38px;
    color: $black-color;
    margin-bottom: 20px;
    max-width: 330px;
  }
  &__right {
    max-width: 640px;
    width: 100%;
    max-height: 550px;
    height: 100%;
    img {
      width: 100%;
      height: 100%;
    }
  }
  @media screen and (max-width: 768px) {
    @include flex;
    &__left {
      align-items: center;
      text-align: center;
    }
    &__right {
      display: none;
    }
  }
  @media screen and (max-width: 360px) {
    &__sub-title {
      font-size: 24px;
    }
  }
}

.spoiler {
  width: 100%;
  margin-bottom: 15px;
  &__wrapper {
    width: 100%;
    padding: 20px 25px;
    cursor: pointer;
    max-height: 65px;
    background: $secondary-color;
    border-radius: 4px;
    @include transition;
    &_active {
      max-height: 1000px;
      background: $secondary-color-dark;
    }
  }
  &__top {
    display: flex;
    svg {
      margin-right: 15px;
    }
    &_active {
      margin-bottom: 10px;
    }
  }
  &__title {
    font-size: 20px;
    color: $black-color;
  }
  &__text {
    transform: scale(0);
    transform-origin: top;
    font-size: 18px;
    color: #5b5855;
    @include transition;
    &_active {
      transform: scale(1);
    }
  }
  @media screen and (max-width: 360px) {
    &__wrapper {
      padding: 10px;
      max-height: 45px;
      &_active {
        max-height: 1000px;
      }
    }
    &__text {
      text-align: left;
    }
  }
}

//                                     NETWORK

.network {
  max-width: 500px;
  padding-top: 30px;
  margin: 0 auto 45px auto;
  text-align: center;
  &__title {
    font-size: 38px;
    color: $footer-color;
    margin-bottom: 35px;
    b {
      font-weight: 400;
      position: relative;
      p {
        display: inherit;
      }
      img {
        position: absolute;
        left: -18px;
        top: -4px;
        z-index: -1;
      }
    }
  }
  &__text {
    font-size: 18px;
    color: $text-color;
    margin-bottom: 40px;
  }
  .button:first-child {
    margin-right: 20px;
  }
  @media screen and (max-width: 350px) {
    .button:first-child {
      margin-right: 0;
    }
  }
}

//                                GRID

.grid {
  display: grid;
  grid-template-columns: repeat(6, minmax(100px, 290px));
  grid-template-rows: repeat(4, minmax(100px, 160px));
  grid-column-gap: 12px;
  grid-row-gap: 12px;
  padding: 45px 0 25px 0;
  margin-bottom: 25px;
  div {
    background-size: cover;
  }
  &__item1 {
    grid-area: 1 / 1 / 3 / 2;
    background: url(../img/grid__item1.png) center center no-repeat;
  }
  &__item2 {
    grid-area: 1 / 2 / 3 / 3;
    background: url(../img/grid__item2.png) center center no-repeat;
  }
  &__item3 {
    grid-area: 1 / 3 / 3 / 4;
    background: url(../img/grid__item3.png) center center no-repeat;
  }
  &__item4 {
    grid-area: 1 / 4 / 4 / 6;
    background: url(../img/grid__item4.png) center center no-repeat;
  }
  &__item5 {
    grid-area: 1 / 6 / 3 / 7;
    background: url(../img/grid__item5.png) center center no-repeat;
  }
  &__item6 {
    grid-area: 3 / 1 / 5 / 3;
    background: url(../img/grid__item6.png) center center no-repeat;
  }
  &__item7 {
    grid-area: 3 / 3 / 5 / 4;
    background: url(../img/grid__item7.png) center center no-repeat;
  }
  &__item8 {
    grid-area: 4 / 4 / 5 / 5;
    background: url(../img/grid__item8.png) center center no-repeat;
  }
  &__item9 {
    grid-area: 4 / 5 / 5 / 6;
    background: url(../img/grid__item9.png) center center no-repeat;
  }
  &__item10 {
    grid-area: 3 / 6 / 5 / 7;
    background: url(../img/grid__item10.png) center center no-repeat;
  }
  @media screen and (max-width: 768px) {
    grid-template-columns: repeat(3, minmax(60px, 240px));
    grid-template-rows: repeat(8, minmax(60px, 120px));
    grid-column-gap: 6px;
    grid-row-gap: 6px;
    div {
      background-size: cover;
    }
    &__item1 {
      grid-area: 1 / 1 / 3 / 2;
    }
    &__item2 {
      grid-area: 1 / 2 / 3 / 3;
    }
    &__item3 {
      grid-area: 1 / 3 / 3 / 4;
    }
    &__item4 {
      grid-area: 3 / 1 / 5 / 3;
    }
    &__item5 {
      grid-area: 3 / 3 / 5 / 4;
    }
    &__item6 {
      grid-area: 5 / 1 / 8 / 3;
    }
    &__item7 {
      grid-area: 8 / 1 / 9 / 2;
    }
    &__item8 {
      grid-area: 8 / 2 / 9 / 3;
    }
    &__item9 {
      grid-area: 5 / 3 / 7 / 4;
    }
    &__item10 {
      grid-area: 7 / 3 / 9 / 4;
    }
  }
}

//                                    COMMENT

.comment {
  background: $secondary-color-light;
  width: 100%;
  padding: 50px 20px;
  position: relative;
  margin-bottom: 50px;
  &__title,
  &__text {
    max-width: 750px;
    width: 100%;
    margin: 0 auto;
    z-index: 2;
    position: relative;
  }
  &__title {
    font-size: 27px;
    color: $primary-color-dark;
    margin-bottom: 35px;
  }
  &__quotes {
    position: absolute;
    top: 12px;
    left: -40px;
  }
  &__text {
    font-size: 18px;
    color: $text-color;
    padding-bottom: 2px;
    a {
      color: $text-color;
      border-bottom: 1px solid $text-color;
    }
  }
  &__line {
    position: absolute;
    right: 0;
    bottom: 20px;
    z-index: 1;
  }
  @media screen and (max-width: 870px) {
    &__quotes {
      left: -15px;
    }
  }
  @media screen and (max-width: 480px) {
    &__title {
      font-size: 22px;
    }
    &__text {
      font-size: 14px;
    }
    &__line {
      width: 150px;
    }
  }
}

.stay-in-touch {
  background: #f1f3ef;
  padding: 65px 0 170px 0;
  &__wrapper {
    max-width: 960px;
    margin: 0 auto;
  }
  &__block-name {
    font-size: 14px;
    color: #8eb391;
    text-align: center;
    margin-bottom: 18px;
    text-transform: uppercase;
  }
  &__title {
    font-size: 38px;
    color: $footer-color;
    margin-bottom: 20px;
    text-align: center;
  }
  &__sub-title {
    font-size: 16px;
    color: $text-color;
    text-align: center;
    max-width: 390px;
    margin: 0 auto 60px auto;
  }
  &__line {
    height: 1px;
    width: 100%;
    background: $secondary-color-dark;
    margin-bottom: 60px;
  }
  &__form {
    width: 100%;
    padding: 0 35px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  &__input {
    flex-basis: 47%;
    margin-bottom: 30px;
    label {
      display: block;
      font-size: 14px;
      color: $text-color;
      margin-bottom: 7px;
    }
    input {
      background: #ffffff;
      border-radius: 4px;
      width: 100%;
      height: 50px;
    }
  }
  &__button {
    padding: 15px 16px 18px 15px;
    background: #0c4a34;
    border: 2px solid #0c4a34;
    border-radius: 4px;
    color: $contrast-color;
    font-size: 13px;
    margin-bottom: 15px;
  }
  &__text{
    flex-basis: 100%;
    font-size: 14px;
    font-weight: 400;
    color: $footer-color;
    padding-bottom: 3px;
    a{
      border-bottom: 1px solid $footer-color;
      padding-bottom: 3px;
    }
  }
  @media screen and (max-width: 768px) {
    padding-bottom: 60px;
    &__input{
      flex-basis: 100%;
    }
  }
}

.for-providers{
  padding: 90px 0 80px 0;
  background: $primary-color;
  // height: 380px;
  position: relative;
  &__img{
    position: absolute;
    top: 0;
    left: 0;
  }
  .container{
    display: flex;
    justify-content: flex-end;
  }
  &__content{
    max-width: 480px;
  }
  &__block-name{
    color: #8eb391;
    font-size: 14px;
    margin-bottom: 14px;
  }
  &__title{
    font-size: 38px;
    color: $contrast-color;
    margin-bottom: 19px;
  }
  &__sub-title{
    font-size: 16px;
    color: $contrast-color;
    margin-bottom: 13px;
  }
  &__link{
    font-size: 16px;
    color: $contrast-color;
    display: flex;
    align-items: center;
    justify-content: center;
    svg{
      margin-left: 5px;
    }
  }
  @media screen and (max-width:768px) {
    padding: 40px 0 30px 0;
    &__img{
      display: none;
    }
    .container{
      justify-content: center;
    }
    &__content{
      max-width: none;
      text-align: center;
    }
  }
}

.featured{
  background: $primary-color-dark;
  padding: 30px 0 25px 0;
  &__text{
    margin-right: 70px;
    max-width: 75px;
    font-size: 14px;
    color: $contrast-color;
  }
  .container{
    display: flex;
    align-items: center;
  }
}

.footer{
  background: $footer-color;
  padding: 60px 0;
  &__top{
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
  }
  &__block{
    flex-basis: 25%;
  }
  &__sub-title, &__text, &__link{
    display: block;
    font-size: 16px;
    color: $contrast-color;
    margin-bottom: 7px;
  }
  &__socials{
    max-width: 125px;
    display: flex;
    justify-content: space-between;
  }
  &__mid{
    text-align: center;
    margin-bottom: 30px;
    img{
      margin-bottom: 15px;
    }
  }
  &__bottom{
    border-top: 1px solid $transparent;
    padding-top: 30px;
    display: flex;
    justify-content: space-between;
    .footer__text, .footer__privacy{
      font-size: 14px;
      color: rgba(255, 255, 255, 0.4);
      max-width: 470px;
    }
  }
  &__privacy{
    max-width: fit-content !important;
  }
  @media screen and(max-width:700px) {
    &__top{
      flex-wrap: wrap;
    }
    &__block{
      flex-basis: 40%;
      margin-bottom: 30px;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
}