@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 400;
}

* {
  font-family: "Roboto", sans-serif;
  line-height: 120%;
}

html {
  scroll-behavior: smooth;
}

.container {
  max-width: 1300px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
}

.button {
  background: transparent;
  padding: 14px 18px;
  border: 2px solid rgba(91, 88, 85, 0.3);
  border-radius: 4px;
  color: #0C4A34;
  transition: all ease 0.5s;
}
.button:hover {
  background: #0C4A34;
  color: #fff;
  border-color: #0C4A34;
}

.header {
  position: relative;
  background: url(../img/header__bg.png) no-repeat center center;
  background-size: cover;
  padding: 20px 0 40px 0;
  overflow: hidden;
}
.header__top {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  margin-bottom: 150px;
}
.header__menu {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  max-width: 670px;
  width: 100%;
}
.header__link {
  display: inline-block;
  font-size: 15px;
  color: #fff;
  position: relative;
}
.header__link::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background: #fff;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.5s;
}
.header__link:hover::after {
  transform: scaleX(1);
  transform-origin: left;
}
.header__button {
  max-width: 230px;
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
.header__button button {
  padding: 8px 16px;
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  font-size: 15px;
  border-radius: 4px;
  transition: all ease 0.5s;
}
.header__button button:hover {
  background: #0C4A34;
}
.header .burger-btn {
  display: none;
  z-index: 10;
  position: relative;
  width: 30px;
  height: 20px;
  cursor: pointer;
}
.header .burger-btn > span {
  position: absolute;
  background-color: #497557;
  width: 100%;
  height: 3px;
  border-radius: 18px;
  left: 0;
  top: 9px;
  transition: all ease 0.5s;
}
.header .burger-btn:after, .header .burger-btn:before {
  content: "";
  position: absolute;
  background-color: #497557;
  width: 100%;
  height: 3px;
  border-radius: 18px;
  left: 0;
  transition: all ease 0.5s;
}
.header .burger-btn:before {
  top: 20px;
}
.header .burger-btn:after {
  bottom: 20px;
}
.header .burger-btn_active span {
  opacity: 0;
}
.header .burger-btn_active:before {
  transform: rotate(45deg);
  top: 9px;
}
.header .burger-btn_active:after {
  transform: rotate(-45deg);
  bottom: 9px;
}
.header__bottom {
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
.header__title {
  font-size: 54px;
  color: #fff;
  margin-bottom: 50px;
}
.header__browse {
  padding: 11px 18px;
  background: #0C4A34;
  color: #fff;
  font-size: 16px;
  border-radius: 4px;
  margin-bottom: 115px;
  transition: all ease 0.5s;
}
.header__faq {
  max-width: 370px;
  width: 100%;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
.header__faq a {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
}
.header__faq a svg {
  margin-left: 20px;
}
.header__pop-up {
  position: absolute;
  right: 25px;
  bottom: 40px;
  max-width: 310px;
  width: 100%;
  padding: 30px 25px;
  text-align: left;
  background: #497557;
  box-shadow: 0px 2px 12px rgba(23, 35, 33, 0.1);
  border-radius: 2px;
  transition: all ease 0.5s;
}
.header__pop-up h3 {
  font-size: 16px;
  color: #fff;
  margin-bottom: 8px;
}
.header__pop-up p {
  font-size: 14px;
  color: #fff;
  margin-bottom: 15px;
}
.header__pop-up button {
  padding: 13px 18px;
  background: rgba(255, 255, 255, 0.2);
  color: #fff;
  font-size: 14px;
  border-radius: 4px;
}
@media screen and (max-width: 768px) {
  .header__menu {
    position: absolute;
    max-width: none;
    text-align: center;
    z-index: 2;
    transform: scaleY(0);
    transform-origin: top;
    left: 0;
    top: -30%;
    width: 100%;
    padding: 70px 20px 20px 20px;
    background: #0C4A34;
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: all ease 0.5s;
  }
  .header__menu_active {
    top: 0;
    transform: scaleY(1);
    box-shadow: 0 0 25px 40px rgba(0, 0, 0, 0.25);
  }
  .header__link {
    margin-bottom: 10px;
  }
  .header__button {
    flex-direction: column;
  }
  .header__button button:first-child {
    margin-bottom: 10px;
  }
  .header .burger-btn {
    display: block;
  }
  .header__title {
    font-size: 36px;
  }
}
@media screen and (max-width: 500px) {
  .header__top {
    margin-bottom: 50px;
  }
  .header__title {
    font-size: 21px;
    margin-bottom: 25px;
  }
  .header__browse {
    margin-bottom: 40px;
  }
}
@media screen and (max-width: 350px) {
  .header {
    padding-bottom: 20px;
  }
  .header__title {
    font-size: 18px;
  }
  .header__faq {
    flex-direction: column;
  }
}

.why-alma {
  padding: 65px 0 35px 0;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: unset;
  flex-direction: row;
}
.why-alma__left {
  max-width: 415px;
  width: 100%;
  display: flex;
  flex-direction: column;
}
.why-alma__title {
  color: #497557;
  font-size: 16px;
  text-transform: uppercase;
  margin-bottom: 15px;
}
.why-alma__sub-title {
  font-size: 38px;
  color: #02291C;
  margin-bottom: 20px;
  max-width: 330px;
}
.why-alma__right {
  max-width: 640px;
  width: 100%;
  max-height: 550px;
  height: 100%;
}
.why-alma__right img {
  width: 100%;
  height: 100%;
}
@media screen and (max-width: 768px) {
  .why-alma {
    display: flex;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    flex-direction: row;
  }
  .why-alma__left {
    align-items: center;
    text-align: center;
  }
  .why-alma__right {
    display: none;
  }
}
@media screen and (max-width: 360px) {
  .why-alma__sub-title {
    font-size: 24px;
  }
}

.spoiler {
  width: 100%;
  margin-bottom: 15px;
}
.spoiler__wrapper {
  width: 100%;
  padding: 20px 25px;
  cursor: pointer;
  max-height: 65px;
  background: #F9F9F7;
  border-radius: 4px;
  transition: all ease 0.5s;
}
.spoiler__wrapper_active {
  max-height: 1000px;
  background: #DCE3DD;
}
.spoiler__top {
  display: flex;
}
.spoiler__top svg {
  margin-right: 15px;
}
.spoiler__top_active {
  margin-bottom: 10px;
}
.spoiler__title {
  font-size: 20px;
  color: #02291C;
}
.spoiler__text {
  transform: scale(0);
  transform-origin: top;
  font-size: 18px;
  color: #5b5855;
  transition: all ease 0.5s;
}
.spoiler__text_active {
  transform: scale(1);
}
@media screen and (max-width: 360px) {
  .spoiler__wrapper {
    padding: 10px;
    max-height: 45px;
  }
  .spoiler__wrapper_active {
    max-height: 1000px;
  }
  .spoiler__text {
    text-align: left;
  }
}

.network {
  max-width: 500px;
  padding-top: 30px;
  margin: 0 auto 45px auto;
  text-align: center;
}
.network__title {
  font-size: 38px;
  color: #02291C;
  margin-bottom: 35px;
}
.network__title b {
  font-weight: 400;
  position: relative;
}
.network__title b p {
  display: inherit;
}
.network__title b img {
  position: absolute;
  left: -18px;
  top: -4px;
  z-index: -1;
}
.network__text {
  font-size: 18px;
  color: #5B5855;
  margin-bottom: 40px;
}
.network .button:first-child {
  margin-right: 20px;
}
@media screen and (max-width: 350px) {
  .network .button:first-child {
    margin-right: 0;
  }
}

.grid {
  display: grid;
  grid-template-columns: repeat(6, minmax(100px, 290px));
  grid-template-rows: repeat(4, minmax(100px, 160px));
  grid-column-gap: 12px;
  grid-row-gap: 12px;
  padding: 45px 0 25px 0;
  margin-bottom: 25px;
}
.grid div {
  background-size: cover;
}
.grid__item1 {
  grid-area: 1/1/3/2;
  background: url(../img/grid__item1.png) center center no-repeat;
}
.grid__item2 {
  grid-area: 1/2/3/3;
  background: url(../img/grid__item2.png) center center no-repeat;
}
.grid__item3 {
  grid-area: 1/3/3/4;
  background: url(../img/grid__item3.png) center center no-repeat;
}
.grid__item4 {
  grid-area: 1/4/4/6;
  background: url(../img/grid__item4.png) center center no-repeat;
}
.grid__item5 {
  grid-area: 1/6/3/7;
  background: url(../img/grid__item5.png) center center no-repeat;
}
.grid__item6 {
  grid-area: 3/1/5/3;
  background: url(../img/grid__item6.png) center center no-repeat;
}
.grid__item7 {
  grid-area: 3/3/5/4;
  background: url(../img/grid__item7.png) center center no-repeat;
}
.grid__item8 {
  grid-area: 4/4/5/5;
  background: url(../img/grid__item8.png) center center no-repeat;
}
.grid__item9 {
  grid-area: 4/5/5/6;
  background: url(../img/grid__item9.png) center center no-repeat;
}
.grid__item10 {
  grid-area: 3/6/5/7;
  background: url(../img/grid__item10.png) center center no-repeat;
}
@media screen and (max-width: 768px) {
  .grid {
    grid-template-columns: repeat(3, minmax(60px, 240px));
    grid-template-rows: repeat(8, minmax(60px, 120px));
    grid-column-gap: 6px;
    grid-row-gap: 6px;
  }
  .grid div {
    background-size: cover;
  }
  .grid__item1 {
    grid-area: 1/1/3/2;
  }
  .grid__item2 {
    grid-area: 1/2/3/3;
  }
  .grid__item3 {
    grid-area: 1/3/3/4;
  }
  .grid__item4 {
    grid-area: 3/1/5/3;
  }
  .grid__item5 {
    grid-area: 3/3/5/4;
  }
  .grid__item6 {
    grid-area: 5/1/8/3;
  }
  .grid__item7 {
    grid-area: 8/1/9/2;
  }
  .grid__item8 {
    grid-area: 8/2/9/3;
  }
  .grid__item9 {
    grid-area: 5/3/7/4;
  }
  .grid__item10 {
    grid-area: 7/3/9/4;
  }
}

.comment {
  background: #F8F4EF;
  width: 100%;
  padding: 50px 20px;
  position: relative;
  margin-bottom: 50px;
}
.comment__title, .comment__text {
  max-width: 750px;
  width: 100%;
  margin: 0 auto;
  z-index: 2;
  position: relative;
}
.comment__title {
  font-size: 27px;
  color: #0C4A34;
  margin-bottom: 35px;
}
.comment__quotes {
  position: absolute;
  top: 12px;
  left: -40px;
}
.comment__text {
  font-size: 18px;
  color: #5B5855;
  padding-bottom: 2px;
}
.comment__text a {
  color: #5B5855;
  border-bottom: 1px solid #5B5855;
}
.comment__line {
  position: absolute;
  right: 0;
  bottom: 20px;
  z-index: 1;
}
@media screen and (max-width: 870px) {
  .comment__quotes {
    left: -15px;
  }
}
@media screen and (max-width: 480px) {
  .comment__title {
    font-size: 22px;
  }
  .comment__text {
    font-size: 14px;
  }
  .comment__line {
    width: 150px;
  }
}

.stay-in-touch {
  background: #f1f3ef;
  padding: 65px 0 170px 0;
}
.stay-in-touch__wrapper {
  max-width: 960px;
  margin: 0 auto;
}
.stay-in-touch__block-name {
  font-size: 14px;
  color: #8eb391;
  text-align: center;
  margin-bottom: 18px;
  text-transform: uppercase;
}
.stay-in-touch__title {
  font-size: 38px;
  color: #02291C;
  margin-bottom: 20px;
  text-align: center;
}
.stay-in-touch__sub-title {
  font-size: 16px;
  color: #5B5855;
  text-align: center;
  max-width: 390px;
  margin: 0 auto 60px auto;
}
.stay-in-touch__line {
  height: 1px;
  width: 100%;
  background: #DCE3DD;
  margin-bottom: 60px;
}
.stay-in-touch__form {
  width: 100%;
  padding: 0 35px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.stay-in-touch__input {
  flex-basis: 47%;
  margin-bottom: 30px;
}
.stay-in-touch__input label {
  display: block;
  font-size: 14px;
  color: #5B5855;
  margin-bottom: 7px;
}
.stay-in-touch__input input {
  background: #ffffff;
  border-radius: 4px;
  width: 100%;
  height: 50px;
}
.stay-in-touch__button {
  padding: 15px 16px 18px 15px;
  background: #0c4a34;
  border: 2px solid #0c4a34;
  border-radius: 4px;
  color: #fff;
  font-size: 13px;
  margin-bottom: 15px;
}
.stay-in-touch__text {
  flex-basis: 100%;
  font-size: 14px;
  font-weight: 400;
  color: #02291C;
  padding-bottom: 3px;
}
.stay-in-touch__text a {
  border-bottom: 1px solid #02291C;
  padding-bottom: 3px;
}
@media screen and (max-width: 768px) {
  .stay-in-touch {
    padding-bottom: 60px;
  }
  .stay-in-touch__input {
    flex-basis: 100%;
  }
}

.for-providers {
  padding: 90px 0 80px 0;
  background: #497557;
  position: relative;
}
.for-providers__img {
  position: absolute;
  top: 0;
  left: 0;
}
.for-providers .container {
  display: flex;
  justify-content: flex-end;
}
.for-providers__content {
  max-width: 480px;
}
.for-providers__block-name {
  color: #8eb391;
  font-size: 14px;
  margin-bottom: 14px;
}
.for-providers__title {
  font-size: 38px;
  color: #fff;
  margin-bottom: 19px;
}
.for-providers__sub-title {
  font-size: 16px;
  color: #fff;
  margin-bottom: 13px;
}
.for-providers__link {
  font-size: 16px;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}
.for-providers__link svg {
  margin-left: 5px;
}
@media screen and (max-width: 768px) {
  .for-providers {
    padding: 40px 0 30px 0;
  }
  .for-providers__img {
    display: none;
  }
  .for-providers .container {
    justify-content: center;
  }
  .for-providers__content {
    max-width: none;
    text-align: center;
  }
}

.featured {
  background: #0C4A34;
  padding: 30px 0 25px 0;
}
.featured__text {
  margin-right: 70px;
  max-width: 75px;
  font-size: 14px;
  color: #fff;
}
.featured .container {
  display: flex;
  align-items: center;
}

.footer {
  background: #02291C;
  padding: 60px 0;
}
.footer__top {
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px;
}
.footer__block {
  flex-basis: 25%;
}
.footer__sub-title, .footer__text, .footer__link {
  display: block;
  font-size: 16px;
  color: #fff;
  margin-bottom: 7px;
}
.footer__socials {
  max-width: 125px;
  display: flex;
  justify-content: space-between;
}
.footer__mid {
  text-align: center;
  margin-bottom: 30px;
}
.footer__mid img {
  margin-bottom: 15px;
}
.footer__bottom {
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  padding-top: 30px;
  display: flex;
  justify-content: space-between;
}
.footer__bottom .footer__text, .footer__bottom .footer__privacy {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.4);
  max-width: 470px;
}
.footer__privacy {
  max-width: fit-content !important;
}
@media screen and (max-width: 700px) {
  .footer__top {
    flex-wrap: wrap;
  }
  .footer__block {
    flex-basis: 40%;
    margin-bottom: 30px;
  }
  .footer__block:last-child {
    margin-bottom: 0;
  }
}